<template>
  <div id='inputwords'>
    <div class="row">
      <div class="col-lg-12 col-md-6 col-xs-6">
        <b-input-group left="词语">
          <b-form-input placeholder="请输入一个词语" v-model:value="value1"></b-form-input>
        </b-input-group>
      </div>
      <div class="col-lg-12 col-md-4 col-xs-4">
        <b-input-group left="大小">
          <b-form-input placeholder="请输入一个整数" v-model:value="value2">
          </b-form-input>
        </b-input-group>
      </div>
      <div class="col-lg-12 col-md-2 col-xs-2 add-word-btn">
        <button class="btn btn-primary" @click="addWord">添加一个词语</button>
      </div>
    </div>
    <div class="row">
      <div class="col-lg-12 col-md-4 col-xs-4">
        <b-input-group left="选择字体">
          <select @change="changeFont($event)" class="w3-select w3-green" name="option">
            <option v-for="item in fontFamilys">{{ item.text }}</option>
          </select>
        </b-input-group>
      </div>
    </div>
  </div>
</template>

<script>
import Bus from '@/assets/Bus'

export default {
  data () {
    return {
      value1: 'Beautiful Word',
      value2: 30,
      selectedFont: 'Tangerine',
      fontValue: 'Tangerine',
      fontFamilys: [
        {
          text: 'Tangerine',
          value: 'Tangerine'
        },
        {
          text: 'Vibur',
          value: 'Vibur'
        },
        {
          text: 'Bubbler One',
          value: 'Bubbler One'
        }
      ]
    }
  },
  methods: {
    enterInput1: function () {

    },
    enterInput2: function () {

    },
    addWord: function () {
      Bus.$emit('addWord', this.value1, this.value2)
    },
    changeFont: function (event) {
      Bus.$emit('eChangeFont', event.target.value)
    }
  }

}
</script>

<style scoped src="../../assets/w3.css">

</style>


<style scoped>
.add-word-btn {
  text-align: right;
}
</style>



