import Vue from 'vue'
import Router from 'vue-router'
import Wellcome from '@/components/Wellcome'
import WordCloud from '@/components/WordCloud'
import PicRecomment from '@/components/PicRecomment'
import ContactUs from '@/components/ContactUs'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Wellcome',
      component: Wellcome
    },
    {
      path: '/wordcloud',
      name: 'wordcloud',
      component: WordCloud
    },
    {
      path: '/pic-recomment',
      name: 'PicRecomment',
      component: PicRecomment
    },
    {
      path: '/contact-us',
      name: 'contact-us',
      component: ContactUs
    }
  ]
})



