<template>
  <div id="dashboard">
    <b-card no-block>
      <b-tabs card ref='tabs'>
        <b-tab title="输入词语">
          <InputWords></InputWords>
        </b-tab>
        <b-tab title="上传文本">
        <Dropzone id="textFileUpload" url="https://httpbin.org/post" v-on:vdropzone-success="showUploadSuccess" :language="uploadLanguage"></Dropzone>
        </b-tab>
      </b-tabs>
    </b-card>
  </div>
</template>

<script>
import InputWords from '@/components/subcomponents/InputWords'
import Dropzone from 'vue2-dropzone'

export default {
  data () {
    return {
      uploadLanguage: {
        dictDefaultMessage: '请上传你的文本'
      }
    }
  },
  components: {
    InputWords,
    Dropzone
  },
  methods: {
    showUploadSuccess: function (file) {
      console.log('a file was successfully uploaded')
    }
  }
}
</script>



