<template>
  <div class="hello container-fluid">
    <!-- Page content -->
    <div class="w3-content" style="max-width:2000px;margin-top:0px">
  
      <!-- Automatic Slideshow Images -->
      <div class="mySlides w3-display-container w3-center">
        <img src="../assets/beautiful-worda.jpg" style="width:100%">
        <div class="w3-display-bottommiddle w3-container w3-text-white w3-padding-32 w3-hide-small">
        </div>
      </div>
      <div class="mySlides w3-display-container w3-center">
        <img src="../assets/beautiful-wordb.jpg" style="width:100%">
        <div class="w3-display-bottommiddle w3-container w3-text-white w3-padding-32 w3-hide-small">
        </div>
      </div>
      <div class="mySlides w3-display-container w3-center">
        <img src="../assets/beautiful-wordc.jpg" style="width:100%">
        <div class="w3-display-bottommiddle w3-container w3-text-white w3-padding-32 w3-hide-small">
        </div>
      </div>
    </div>
    <!-- The Band Section -->
  
    <div class="w3-panel w3-lenter w3-padding-64" style="max-width:800px; margin-top: 20px;" id="band">
      <div class="w3-panel w3-leftbar w3-sand w3-xxlarge w3-serif">
        <h1>
          <span style="font-size:150px;line-height:0.6em;opacity:0.2 ">&#10077;</span>
          <i>Wellcome to the Beautiful Word</i>
        </h1>
      </div>
  
    </div>
  </div>
</template>

<script>
export default {
  name: 'welcome',
  data () {
    return {
      msg: 'Welcome to Beautiful word'
    }
  },
  mounted () {
    let x = document.getElementsByClassName('mySlides')
    let y = 0
    setInterval(function () {
      for (let i = 0; i < x.length; i++) {
        x[i].style.display = 'none'
      }
      y++
      if (y > x.length) {
        y = 1
      }
      x[y - 1].style.display = 'block'
    }, 1000)
  }
}
</script>

<style scoped>
h1,
h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}
</style>



