<template>
  <div class='tagbar w3-card-4 '>
    <ul class="w3-ul w3-border">
      <li class="list-header w3-blue">
        <h3>词语列表</h3>
      </li>
      <li v-for="(item, index) in words" :class="[colorList[index % 9], others]" :key="index">{{ item[0] }}
        <span @click="delTag(index)" class=" w3-display-right deleteButton">
          <button class="w3-btn" type="" href="">&times;</button>
        </span>
      </li>
    </ul>
  </div>
</template>

<script>
import Bus from '@/assets/Bus'
export default {
  data () {
    return {
      words: [
        ['Beautiful Word', 50]
      ],
      liColor: 'w3-green',
      others: 'w3-display-container',
      colorList: [
        'w3-green',
        'w3-red',
        'w3-pink',
        'w3-orange',
        'w3-yellow',
        'w3-lime',
        'w3-khaki',
        'w3-purple',
        'w3-blue'
      ]
    }
  },
  computed: {
    liClass: function () {
      return {
        liColor: function () {
          let r = Math.floor(Math.random() * 9)
          this.liColor = this.colorList[r]
        }
      }
    }
  },
  methods: {
    delTag: function (index) {
      let c = this.colorList.splice(index % 9, 1)
      this.colorList.push(c)
      Bus.$emit('delWord', index)
    }
  },
  mounted: function () {
    let self = this
    Bus.$on('updateWords', function (words) {
      self.words = words
    })
  }
}

</script>

<style scoped src="../assets/w3.css">

</style>

<style scoped>
.tag {
  margin-bottom: 2px;
  margin-left: 2px;
}

.tagbar {
  width: 100%;
  height: 400px;
  overflow: scroll;
  background-color: '#cccccc';
  font-size: 10px;
}

.list-header {
  text-align: center;
}

.deleteButton {
  font-size: 15px;
  color: 'aqua';
}
</style>




