<template>
  <div class='wordcloud container-fluid'>
    <div class="row">
      <div class="col-lg-8 col-md-12 col-xs-12">
        <div class="row">
          <div class="canvas-container col-lg-8 col-md-8 col-xs-8">
            <WCcanvas></WCcanvas>
          </div>
          <div class="tags-container col-lg-4 col-md-4 col-xs-4">
            <TagBar></TagBar>
          </div>
        </div>
      </div>
      <div class="dashboard col-lg-4 col-md-12 col-xs-12">
        <Dashboard></Dashboard>
        <div id="btns">
          <div class="row">
            <div class="col">
              <b-button-group size="lg">
                <b-button size="md" variant="success" @click="generateWordCloud">
                  生成词云
                </b-button>
                <b-button size="md" @click="downloadCanvas" variant='primary'>
                  下载图片
                </b-button>
                <b-button size="md" @click="resetWC" variant='danger'>
                  重置
                </b-button>
              </b-button-group>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Dashboard from '@/components/Dashboard'
import WCcanvas from '@/components/Canvas'
import TagBar from '@/components/TagBar'
import Bus from '@/assets/Bus'

export default {
  name: 'WordCloud',
  data () {
    return {
    }
  },
  methods: {
    generateWordCloud: function () {
      Bus.$emit('generateWordCloud')
    },
    resetWC: function () {
      Bus.$emit('resetWC')
    },
    downloadCanvas: function () {
      Bus.$emit('downloadImg')
    }
  },
  components: {
    Dashboard,
    WCcanvas,
    TagBar
  }
}

</script>

<style scoped>
#btns {
  text-align: right;
}

.tags-container {
  text-align: left;
}
</style>



