<template>
  <div class="canvas-container">
    <canvas id="canvas" :width="canvasWidth" :height="canvasHeight">
    </canvas>
  </div>
</template>

<script>
import WordCloud from 'wordcloud'
import DownloadCanvas from 'download-canvas'
import Bus from '@/assets/Bus'

export default {
  name: 'WordCloud',
  data () {
    return {
      canvasWidth: '700px',
      canvasHeight: '400px',
      words: [
        ['Beautiful Word', 50]
      ],
      minRotation: 0,
      maxRotation: 3.1415 / 2,
      fontFamily: 'Tangerine'
    }
  },
  watch: {
    words: function () {
      Bus.$emit('updateWords', this.words)
    }
  },
  methods: {
    generateWC: function () {
      let option = {
        // Presentation
        list: this.words,
        fontFamily: this.fontFamily,
        fontWeight: 'normal',
        color: 'random-dark',
        classes: null,
        minSize: 1,
        minRotation: this.minRotation,
        maxRotation: this.maxRotation,
        weightFactor: 1,
        clearCanvas: true,
        rotationSteps: 2,
        backgroundColor: '#eeeeee',
        shuffle: 12,
        rotateRatio: 0.5,
        // Dimension
        gridSize: 10,
        // orgin: [x, y],
        drawOutOfBound: true
        // Mask
        // drawMask: true,
        // maskColor: 'pink',
        // maskGapWidth: 1,
        // shape
        // circle(default), cardioid, diamond, triangle-forward, triangle, star
        // shape: 'circle',
        // ellipticity: null,
        // Interactive
        // hover: null,
        // click: null,
        // Events
        // wordcloudstart: null,
        // wordclouddrawn: null,
        // wordcloudstop: null,
        // wordcloudabort: null
      }
      let can = document.getElementById('canvas')
      WordCloud(can, option)
    },
    resetWC: function () {
      this.words = []
    },
    downloadImg: function () {
      DownloadCanvas.downloadCanvas('canvas', {
        name: 'beautiful-word',
        type: 'jpg',
        quality: 1
      })
    }
  },
  mounted () {
    let self = this
    self.generateWC()
    Bus.$on('addWord', function (v1, v2) {
      self.words.push([v1, v2])
      self.generateWC()
    })
    Bus.$on('generateWordCloud', function () {
      self.generateWC()
    })
    Bus.$on('resetWC', function () {
      self.words = []
      self.generateWC()
    })
    Bus.$on('delWord', function (index) {
      self.words.splice(index, 1)
      self.generateWC()
    })
    Bus.$on('downloadImg', function () {
      self.downloadImg()
    })
    Bus.$on('eChangeFont', function (font) {
      self.fontFamily = font
      self.generateWC()
    })
  }
}
</script>

<style scoped>
@import url('https://fonts.googleapis.com/css?family=Vibur');
@import url('https://fonts.googleapis.com/css?family=Tangerine');
@import url('https://fonts.googleapis.com/css?family=Bubbler+One|Vibur');
</style>

<style scoped>
.canvas-container {
  text-align: center;
  overflow: scroll;
}

#btns {
  text-align: right;
}
</style>



